.approach {
  background-color: #fff;
  padding: 60px 0;
  &__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__item {
    width: 45%;
    padding-left: 195px;
    &-title {
      font-weight: 900;
      font-size: 94px;
      line-height: 98px;
      color: #8cc871;
      margin-bottom: 30px;
    }
    &-contents {
      font-family: "Noto Serif";
      font-weight: 400;
      font-style: italic;
      color: #2a4425;
      font-size: 29px;
      line-height: 36px;
      margin-bottom: 40px;
    }
    &-content {
      margin-bottom: 28px;
      padding-left: 28px;
      position: relative;
      font-weight: 700;
      &::before {
        content: "";
        left: 0;
        top: 0;
        bottom: 0;
        position: absolute;
        width: 13px;
        height: 100%;
        background-color: #8cc871;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    &-btn {
      display: inline-block;
      outline: none;
      border: none;
      cursor: pointer;
      background-color: #8cc871;
      color: #fff;
      text-transform: uppercase;
      border-radius: 10px;
      padding: 14px 13px 13px;
      display: flex;
      margin: 0 auto;
      font-weight: 700;
      font-style: italic;
      font-size: 26px;
    }
  }
  &__gallery {
    width: 890px;
    padding-right: 23px;
    padding-bottom: 60px;
    position: relative;

    .slick-dots {
      position: absolute;
      bottom: -50px;
      left: 50%;
      transform: translateX(-50%);
      li {
        margin-right: 15px;
        &:last-child {
          margin-right: 0;
        }
        &.slick-active button {
          background-color: #8cc871;
        }
        button {
          border: none;
          cursor: pointer;
          background-color: transparent;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          background-color: #294424;
          outline: none;
          &::before {
            font-size: 0;
          }
        }
      }
    }

    &-item {
      height: 890px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

@media (max-width: 1800px) {
  .approach {
    &__box {
      flex-direction: column;
    }
    &__item {
      padding-left: 20px;
    }
    &__gallery {
      width: 52%;
    }
  }
}

@media (max-width: 1800px) {
  .approach {
    &__item {
      &-title {
