        margin-bottom: 28px;
      }
    }
    &__box {
      flex-direction: column;
      justify-content: center;
    }
    &__gallery {
      width: 910px;
      padding-right: 0;
    }
    &__item {
      margin-bottom: 60px;
      width: 50%;
    }
  }
}

